<template lang="pug">
  .verfications.fullheight.is-flex.valign.flex-column
    h1.h2.is-size-2.text-center.d-block
      | Verifications
    p.is-size-6 Gain more visibility by verifying your account

    steps(:step="step")


    transition(name="slide-fade" mode="out-in")
      .card.shadow.rounded.p-5.mt-4(key="phone_number" v-if="step == 0")
        //- PHONE NUMBER
        .form-group.has-text-left
          p Please enter your phone number
          phone-number-verification(v-model="form.phone_number")

      .card.shadow.rounded.p-5.mt-4(key="phone_number" v-if="step == 1")
        //- DOCUMENT
        .form-group.has-text-left
          file-dropzone(@file-upload="fileUploadSuccess")

      div(key="phone_number" v-if="step == 2")
        .card.shadow.rounded.mt-4.position-relative
          //- ID
          .instructions
            p.m-0.h4.text-dark ID verification
            small.is-size-7 Book a Whatsapp video call to verify your ID
          iframe(:src="`https://calendly.com/guillaume-voisin/30min?name=${user.first_name}&email=${user.email}`")
        .text-right.mt-3
          b-button(variant="primary" @click="confirmBooking" :class="{ loading }") Next

      .card.shadow.rounded.p-5.mt-4.card--all-good(key="all_good" v-if="step == 3")
        p.text-success(v-if="user.verification_status == 'call_booked'")
          | We're done for now. We will call you to end the verification process.<br>Make sure to have an ID at hand when we do so.
        p.text-success(v-if="user.verification_status == 'done'")
          | We're all good!

</template>

<script>
  import {mapGetters, mapActions} from "vuex";
  import Steps from "../ui/steps";
  import PhoneNumberVerification from "../ui/phone-number-verification";
  import FileDropzone from "../ui/file-dropzone";

  export default {
    name: "Verifications",
    components: {
      FileDropzone,
      PhoneNumberVerification,
      Steps
    },
    props: [],
    data() {
      return {
        // FORM
        loading: false,
        form: {
          phone_number: null,
        },
        documentValidated: false
      };
    },

    computed: {
      ...mapGetters([ 'user', 'attachments' ]),
      step() {
        if (!this.user.phone_confirmed_at) {
          return 0
        } else if (!this.document) {
          return 1
        } else if (this.user.verification_status == 'pending') {
          return 2
        } else {
          return 3
        }
      },
      document() {
        return this.attachments.find(att => att.document)
      }
    },
    created() {
      this.getAttachments({
        model_type: 'User',
        model_id: this.user.id
      })
    },
    mounted() {
      this.form.phone_number = this.user.phone_number
    },
    methods: {
      ...mapActions(['getAttachments', 'updateUser', 'setUser']),
      fileUploadSuccess() {
        this.$toast.success(`We will check your document and reach back to you ASAP`)
      },
      confirmBooking() {
        this.loading = true
        this.updateUser({
          ...this.user,
          verification_status: 'call_booked'
        }).then((res) => {
          this.setUser(res.body.data.attributes)
          this.false = true
        })
      }
      // initStep() {
      //   if (!this.user.phone_confirmed_at) {
      //     this.step = 0
      //   } else if (!this.document) {
      //     this.step =  1
      //   } else if (this.document) {
      //     this.step =  2
      //   }
      // }
    },
  };
</script>


<style lang="scss">
  @import 'colors';

  .verfications {
    background-color: $sweetGray;
    h1 {
      text-transform: none;
    }

    .card {
      min-width: 470px;
      transition: width .15s ease;
    }

    .card--all-good {
      max-width: 470px;
    }

    .instructions {
      position: absolute;
      top: 0.5rem;
      left: 50%;
      height: 45px;
      width: 280px;
      margin-left: -140px;
      background: #FFF;
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    iframe {
      border: 0 none;
      min-width: 470px;
      height: 570px;
    }
  }
</style>
