<template lang="pug">
  .ui--file-dropzone
    .main-file.valign

      .dropzone.text-grey.px-5(
        v-if="!attachments[0]"
        :class="{'dropzone--hover': dragging, isLoading: uploading}"
        @dragenter.stop.prevent="dragging = true"
        @dragover.stop.prevent=""
        @dragleave.stop.prevent="dragging = false"
        @drop.stop.prevent="onDrop($event)"
      )
        | DRAG & DROP YOUR PDF DOCUMENT HERE...
      a.file(target="_blank" :href="attachments[0].document.url" v-if="attachments[0] && attachments[0].document")
        i.mdi.is-size-1 attachment
        p
          | {{basename}}

</template>

<script>
  import {mapGetters, mapActions} from "vuex";

  export default {
    name: "FileDropzone",
    components: {
    },

    props: [],
    data() {
      return {
        // UX
        dragging: false,
        uploading: false,
        isLoading: false
      };
    },
    computed: {
      ...mapGetters(['attachments', 'user']),
      basename() {
        const str = this.attachments[0].document.url
        var base = new String(str).substring(str.lastIndexOf('/') + 1);
        if(base.lastIndexOf(".") != -1)
          base = base.substring(0, base.lastIndexOf("."));
        return base;
      }
    },
    mounted() {
    },
    methods: {
      ...mapActions(['saveAttachment']),

      onDrop(ev) {
        const {dataTransfer} = ev;
        this.uploading = true;
        this.saveAttachment({
          file: dataTransfer.files[0],
          model_id: this.user.id,
          model_type: 'User'
        }).then(res => {
          this.uploading = false;
          this.resetUploadingUi()
          this.$emit('file-uploaded')
        }, err => {
          this.$toast.error('The file sent couldn\'t be processed. Please make sure you send a PDF file.')
          this.resetUploadingUi()
        });
      },

      resetUploadingUi() {
        this.dragging = false
        this.uploading = false
      }
    },
  };
</script>


<style lang="scss">
  .ui--file-dropzone {
    background-size: contain;
    background-position: center center;
    background-repeat: no-repeat;
    .main-file {
      text-align: center;
      min-height: 300px;
      .dropzone {
        min-height: 300px;
      }
      img {
        height: 270px;
        width:  auto
      }
      file {
        width: 100%;
      }
    }
  }
</style>
